@import "../index.sass";

// CURIOSITIES SECTION

.curiositiesSection {
    height: 100vh;
    @include flexCenter();
    .navCuriosities {
        position: relative;
        width: 60%;
        height: 60%;
        @include flexCenter();
        justify-content: space-around;
        margin-top: 30px;
        z-index: 1;
        .curiositiesNavBtn {
            position: relative;
            @include flexCenter();
            width: 47%;
            height: 40%;
            margin: 20px 0;
            border: 3px solid $white;
            color: $white;
            text-decoration: none;
            font-size: 30px;
            transition: .5s;
            background-color: #000;
            .description {
                position: absolute;
                top: 50%;
                width: 50%;
                height: 90%;
                @include flexCenter();
                text-align: center;
                font-size: 20px;
                transform: translateY(-50%);
                transition: .5s;
                z-index: -1;
                opacity: 0; } }
        .curiositiesNavBtn:hover {
            letter-spacing: 1px;
            background-color: $white;
            color: $black; }
        .curiositiesNavBtn:nth-of-type(1), .curiositiesNavBtn:nth-of-type(3) {
            .description {
                left: -50%;
                transform: translate(100%, -50%); } }
        .curiositiesNavBtn:nth-of-type(2), .curiositiesNavBtn:nth-of-type(4) {
            .description {
                right: -50%;
                transform: translate(-100%, -50%); } }
        .curiositiesNavBtn:hover {
            .description {
                transform: translateY(-50%);
                opacity: 1;
                color: $white;
                letter-spacing: normal; } } } }
.backToCuriositiesBtn {
    position: fixed;
    padding: 5px 10px;
    z-index: 1;
    left: 10px;
    bottom: 100px;
    color: $white;
    background-color: $black;
    font-size: 25px;
    text-decoration: none;
    transition: .3s;
    animation: backToCuriositiesBtnMove 2s linear infinite;
    .fa-arrow-left {
        margin-right: 5px; } }
.backToCuriositiesBtn:hover {
    background-color: $white;
    color: $black; }
@keyframes backToCuriositiesBtnMove {
    33% {
        transform: translateX(10px); }
    66% {
        transform: translateX(-10px); }
    100% {
        transform: none; } }


// WORKSHOPS SECTION

.mainSection.workshopsSection {
    height: 100vh;
    .workshopsImgContainer {
        width: 60%;
        height: 60%;
        @include flexCenter();
        justify-content: space-around;
        .img {
            width: 15%;
            height: 20%;
            margin: 20px 0;
            @include flexCenter();
            img {
                height: 100%; } } } }

// EXPLORATION SECTION

.mainSection.explorationSection {
    height: 100vh;
    align-items: flex-end;
    align-content: stretch; }

// COMICS-DUMMY SECTION

.mainSection.comicsDummy {
    height: 100vh;
    .comicsDummyImgContainer {
        width: 60%;
        height: 60%;
        @include flexCenter();
        justify-content: space-around;
        .img {
            width: 23%;
            height: 17%;
            margin: 20px 0;
            @include flexCenter();
            img {
                height: 100%; } } } }


// COMICS-ILLUSTRATION SECTION

.mainSection.comicsIllustrationSection {
    position: relative;
    .img {
        height: 100px;
        transition: .5s;
        cursor: pointer;
        img {
            height: 100%; } } }

